@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@700&display=swap');
:root{
  --airnd-color:#FF5A5F;
  --primary-font-style:'Josefin Sans', sans-serif;
}
*{
  margin: 0;
  font-family: var(--primary-font-style);
}
/*nav bar style */
.nav-bar{
  display: flex;
  background-color: #ffff;
  box-shadow: 0px 2.98256px 7.4564px rgba(0, 0, 0, 0.1);

}
.logo{
  width: 6%;
  margin-right: -0.9rem;
  margin-left: clamp(0.5em, 1vw, 2rem);
}
.logo-image{
  width: 100%;
}
.logo-text{
  color: var(--airnd-color);
  margin: auto 0;
  font-size: clamp(1em, 3vw, 3rem);
}

@media (max-width:770px){
.logo-text{
  padding-left: 0.5rem;
}
}
@media (max-width:400px){
  .logo{
    width: 11%;
  }
}

/* hero section design */
.image-container{
  width: 60%;
  margin: 4rem auto;
}
.hero-image{
width: 100%;
}
.hero-title{
  color: black;
  font-size: clamp(1.5em, 4.4vw, 5rem);
  margin: 1.5rem 0;
}
.hero-info{
  color: #222222;
  font-size: clamp(0.8em, 1.7vw, 1.4rem);
  margin: 1rem 0;
  width: 40%;
  font-weight: 100;
  word-spacing: 0.2rem;
  line-height: clamp(1em, 2.7vw, 2rem);
}
.hero-text{
  width: 88%;
  margin: 0.5rem auto;
  text-align: left;
}

/* card style */
.card{
  width: 40rem;
  color: #ffff;
  margin: 1rem;
  transition: all 500ms;
  position:relative ;
}
.card-badge{
  background-color: #ffff;
  border: 1px solid transparent;
  border-radius: 4px;
  color: rgb(26, 26, 26);
  position: absolute;
  top: 8px;
  left: 8px;
  padding: 0.18rem 0.3rem;
  font-family: Arial, Helvetica, sans-serif;
  font-size: clamp(0.5em, 1vw, 1rem);


}
.card-image{
  width: 100%;
}
.card-status{
  display: flex;
  align-items: center;
  margin: 0.3rem 0;
}
.star{
  width: 20px;
}
.gray{
  color: #aaa;
}
.card-title{
  font-size: clamp(1em, 2vw, 1.1rem);
  color: #272727;
  margin: 1rem 0;
}
.event-price{
  font-size: clamp(1em, 2vw, 1.2rem);
  color: #222222;
  margin: 0.5rem 0;
}

.flex{
  display: flex;
  flex-direction: row;
  width: 75%;
  overflow: auto;
  margin: 1rem auto;
}